<template>
  <div class="basic-setting">
    <label class="components">No. of Characters</label>
    <input
      min="1"
      max="20"
      class="components"
      type="number"
      name="totalCharacter"
      @change="$emit('basic-setting', this.totalCharacter)"
      v-model="totalCharacter"
    />
    <Button
      class="components"
      @btn-click="$emit('toggle-advance-setting')"
      :text="showAdvanceSetting ? 'close' : 'advance'"
      :color="showAdvanceSetting ? 'dimgray' : this.colour"
    />
  </div>
</template>
<script>
import Button from "./Button.vue";

export default {
  name: "BasicSetting",
  props: {
    showAdvanceSetting: Boolean,
    colour: String,
  },
  components: {
    Button,
  },
  emits: ["basic-setting", "toggle-advance-setting"],
  data() {
    return {
      totalCharacter: 3,
    };
  },
  emits: ["basic-setting"],
};
</script>

<style scoped>
.basic-setting {
  display: flex;
  align-items: center;
  margin: 30px 0px;
  /* height: 70vh; */
  justify-content: space-between;
}
input {
  border: none;
  box-shadow: 0px 0px 10px rgba(174, 174, 174, 0.75);
  border-radius: 10px;
  text-align: center;
}
input:focus {
  border: 1px solid dimgray;
}

@media (min-width: 300px) {
  label {
    font-size: 0.8rem;
  }
  .components {
    display: block;
  }
  input {
    width: 20%;
    padding: 3px;
  }
}
@media (min-width: 425px) {
  input {
    padding: 6px;
    width: 40%;
  }
  label {
    font-size: 0.8rem;
  }
}

/* TABLET */
@media (min-width: 768px) {
  label {
    font-size: 1rem;
  }
  .components {
    display: inline-block;
  }
}

