<template>
  <Header
    title="affection table"
    description="Affection table discloses the overall relationship by revealing affection level of one character towards another, while affection towards oneself indicates self-confidence."
  />
  <table class="tbl">
    <tr>
      <td
        class="title"
        rowspan="2"
        :style="{ 'background-color': this.setColour('dark') }"
      >
        Character
      </td>
      <td
        class="title"
        :style="{ 'background-color': this.setColour('dark') }"
        :colspan="characters.length + 1"
      >
        Affection towards
      </td>
    </tr>
    <tr>
      <!-- <th>character</th> -->
      <th
        :style="{ 'background-color': this.setColour() }"
        :key="index"
        v-for="(character, index) in characters"
      >
        {{ index + 1 }}
      </th>
    </tr>
    <tr :key="rowIndex" v-for="(character, rowIndex) in characters">
      <th :style="{ 'background-color': this.setColour() }">
        {{ rowIndex + 1 }}
      </th>
      <td :key="colIndex" v-for="(character, colIndex) in characters">
        {{ Math.floor(Math.random() * 100) }}
      </td>
    </tr>
  </table>
</template>
<script>
import Header from "./Header.vue";

export default {
  name: "Table",
  computed: {},
  data() {
    return {
      hovered: false,
    };
  },
  methods: {
    setColour(option) {
      console.log("this table shit is working");
      var output = "";
      if (option === "dark") {
        output = "hsl(" + this.colour.hue + "," + 75 + "%," + 30 + "%)";
      } else {
        output = "hsl(" + this.colour.hue + "," + 40 + "%," + 50 + "%)";
      }
      return output;
    },
  },
  components: {
    Header,
  },
  props: {
    characters: Array,
    colour: Object,
  },
};
</script>
<style scoped>
table {
  width: 100%;
  background-color: white;
  opacity: 0.9;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  text-align: center;
  padding: 20px 20px;
}

th {
  /* background-color: gray; */
  color: white;
}
th,
td {
  /* border: 1px solid black; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  text-align: center;
  padding: 20px 20px;
}
th,
td {
  margin: 3px;
  padding: 5px 0px;
  opacity: 0.8;
}
.title {
  color: white;
}
@media (min-width: 300px) {
  th {
    font-size: 0.6rem;
  }
  td {
    font-size: 0.5rem;
  }
}
@media (min-width: 425px) {
  th {
    font-size: 0.75rem;
  }
  td {
    font-size: 0.75rem;
  }
}

/* TABLET */
@media (min-width: 768px) {
  th {
    font-size: 1rem;
  }
  td {
    font-size: 1rem;
  }
}

/* DESKTOP */
@media (min-width: 1024px) {
}
</style>
