<template>
  <div class="content">
    <Header
      title="dramatic generator"
      description="Dramatic Generator is a tool generating characters with randomly dramatic attributes as well as plotting randomly dramatic relationship between them. *For imaginative users only"
    />
    <AdvanceSetting
      @generate="handleGenerate"
      @change-advSetting="handleChange"
      :settings="settings"
      :colour="testFunction('dark')"
    />
    <transition name="fade">
      <router-view
        :characters="characters"
        :colour="colour"
        :showCharacters="showCharacters"
        :testFunction="testFunction"
      />
    </transition>
    <Footer />
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Footer from "./components/Footer.vue";
import AdvanceSetting from "./components/AdvanceSetting.vue";
import Characters from "./components/Characters.vue";

export default {
  name: "App",
  components: {
    Header,
    Footer,
    AdvanceSetting,
    Characters,
  },
  computed: {
    computed: {},
  },
  methods: {
    testFunction(option) {
      var output = "";
      if (option === "dark") {
        output = "hsl(" + this.colour.hue + "," + 75 + "%," + 30 + "%)";
      } else if (option === "light") {
        output = "hsl(" + this.colour.hue + "," + 40 + "%," + 50 + "%)";
      } else {
        output =
          "hsl(" +
          this.colour.hue +
          "," +
          this.colour.saturation +
          "%," +
          this.colour.lightness +
          "%)";
      }
      // console.log("table colour:  " + output);
      return output;
    },
    setBackground(bgColour) {
      const output = "radial-gradient(" + this.getColourString() + ", white)";
      document.querySelector("html").style.backgroundImage = output;
    },
    getColour() {
      const hue = 360 * Math.random();
      const saturation = 25 + 70 * Math.random();
      const lightness = 85 + 10 * Math.random();
      return { hue, saturation, lightness };
    },
    getColourString() {
      const colourName =
        "hsl(" +
        this.colour.hue +
        "," +
        this.colour.saturation +
        "%," +
        this.colour.lightness +
        "%)";

      return colourName;
    },
    checkHoroscope(date) {
      // console.log("The date is " + date.getTime());
      const result = this.horoscopes.filter(
        (sign) =>
          date.getTime() >= sign.start.getTime() &&
          date.getTime() <= sign.end.getTime()
      );
      if (!result) {
        console.log("shit!");
      } else {
        return result[0].horoscope;
      }
    },
    handleChange(ObjectToChange) {
      try {
        var settingIndex = ObjectToChange.settingIndex;
        var optionIndex = ObjectToChange.optionIndex;

        this.settings[settingIndex].options[optionIndex].optionCheck =
          !this.settings[settingIndex].options[optionIndex].optionCheck;
      } catch (error) {
        console.log(error);
        return;
      }
    },
    handleGenerate(totalCharacter) {
      this.characters = [];

      // check Character limit
      if (totalCharacter > 20 || totalCharacter < 0) {
        alert("No. of Characters must falls between 0 & 20");
        return;
      }

      // if horoscope, check birthday
      if (this.settings[1].options[1].optionCheck) {
        this.settings[0].options[2].optionCheck = true;
      }

      for (var i = 0; i < totalCharacter; i++) {
        const newCharacter = [];
        var horoscope;
        this.settings.forEach((setting) => {
          setting.options.forEach((option) => {
            if (option.optionCheck) {
              var tempValue;

              switch (option.optionName) {
                case "gender":
                  tempValue = Math.floor(Math.random() * 2) ? "male" : "female";
                  break;
                case "birthday":
                  var date = new Date(
                    Math.floor(Math.random() * 1000 * 60 * 60 * 24 * 365.25)
                  );
                  date.setHours(0, 0, 0, 0);

                  tempValue = date.toLocaleString("default", {
                    day: "numeric",
                    month: "short",
                  });
                  horoscope = this.checkHoroscope(date);
                  // console.log("horoscope checked " + horoscope);
                  break;

                case "mbti":
                  tempValue = "";
                  const mbti = [
                    ["E", "I"],
                    ["S", "N"],
                    ["T", "F"],
                    ["P", "J"],
                  ];
                  for (var i = 0; i < 4; i++) {
                    tempValue += mbti[i][Math.floor(Math.random() * 2)];
                  }
                  break;

                case "horoscope":
                  tempValue = horoscope;
                  break;
                default:
                  tempValue = Math.floor(Math.random() * 100);
                  break;
              }

              const temp = {
                feature: option.optionName,
                value: tempValue,
              };
              newCharacter.push(temp);
            }
          });
        });
        this.characters.push(newCharacter);
      }
      // console.log(this.characters);
      // <router-link to="/characterCard" />;
      this.showCharacters = true;
      this.colour = this.getColour();
      this.setBackground(this.getColourString());
    },
  },
  data() {
    return {
      colour: [],
      showCharacters: false,
      characters: [],
      horoscopes: [
        {
          horoscope: "capricorn",
          start: new Date(1970, 0, 1),
          end: new Date(1970, 0, 19),
        },
        {
          horoscope: "aquarius",
          start: new Date(1970, 0, 20),
          end: new Date(1970, 1, 18),
        },
        {
          horoscope: "pisces",
          start: new Date(1970, 1, 19),
          end: new Date(1970, 2, 20),
        },
        {
          horoscope: "aries",
          start: new Date(1970, 2, 21),
          end: new Date(1970, 3, 19),
        },
        {
          horoscope: "taurus",
          start: new Date(1970, 3, 20),
          end: new Date(1970, 4, 20),
        },
        {
          horoscope: "gemini",
          start: new Date(1970, 4, 21),
          end: new Date(1970, 5, 20),
        },
        {
          horoscope: "cancer",
          start: new Date(1970, 5, 21),
          end: new Date(1970, 6, 22),
        },
        {
          horoscope: "leo",
          start: new Date(1970, 6, 23),
          end: new Date(1970, 7, 22),
        },
        {
          horoscope: "virgo",
          start: new Date(1970, 7, 23),
          end: new Date(1970, 8, 22),
        },
        {
          horoscope: "libra",
          start: new Date(1970, 8, 23),
          end: new Date(1970, 9, 22),
        },
        {
          horoscope: "scorpio",
          start: new Date(1970, 9, 23),
          end: new Date(1970, 10, 21),
        },
        {
          horoscope: "sagittarius",
          start: new Date(1970, 10, 22),
          end: new Date(1970, 11, 21),
        },
        {
          horoscope: "capricorn",
          start: new Date(1970, 11, 22),
          end: new Date(1970, 11, 31),
        },
      ],
      settings: [
        {
          name: "basic",
          options: [
            { optionName: "age", optionCheck: false },
            { optionName: "gender", optionCheck: true },
            { optionName: "birthday", optionCheck: true },
          ],
        },
        {
          name: "personality",
          options: [
            { optionName: "mbti", optionCheck: true },
            { optionName: "horoscope", optionCheck: true },
          ],
        },
        {
          name: "attribute",
          options: [
            { optionName: "intellectual", optionCheck: true },
            { optionName: "appearance", optionCheck: true },
            { optionName: "education", optionCheck: true },
            { optionName: "size", optionCheck: true },
            { optionName: "wealth", optionCheck: true },
            { optionName: "luck", optionCheck: true },
          ],
        },
      ],
    };
  },
};
</script>

