<template>
  <div>
    <Header
      title="characters"
      description="Filter desired character attributes in advance setting."
    />
    <div class="card-group">
      <div :key="index" v-for="(character, index) in characters">
        <Character
          :style="{ 'background-color': getColour(index) }"
          :character="character"
          :index="index"
        />
      </div>
    </div>
  </div>
</template>
<script>
import Header from "./Header.vue";
import Character from "./Character.vue";

export default {
  name: "Characters",
  components: {
    Header,
    Character,
  },
  props: {
    characters: Array,
    colour: Object,
  },
  methods: {
    getColour(index) {
      var h = [
        this.colour.hue < 180
          ? this.colour.hue +
            (index * (360 - this.colour.hue)) / this.characters.length
          : this.colour.hue -
            index * (this.colour.hue / this.characters.length),
      ];
      var h = [
        this.colour.hue - index * 3 * this.characters.length >= 0
          ? this.colour.hue - index * 3
          : this.colour.hue + index * 3,
      ];
      var s = [
        this.colour.saturation < 60
          ? this.colour.saturation + index * 2
          : this.colour.saturation - index * 2,
      ];
      var l = [
        this.colour.lightness < 47
          ? this.colour.lightness + index
          : this.colour.lightness - index,
      ];
      const output = "hsl(" + h + "," + s + "%," + l + "%)";
      // const output =

      return output;
    },
  },
};
</script>
<style scoped>
.card-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-content: space-between;
  /* margin: 0px 20px; */
  gap: 20px;
  transition: 3s;
}
</style>
