<template>
  <div :key="index" v-for="(setting, index) in settings">
    <h3>{{ setting.name }}</h3>
    <div
      class="form-group"
      :key="optIndex"
      v-for="(option, optIndex) in setting.options"
    >
      <Setting
        @change-setting="handleChange"
        :settings="settings"
        :settingIndex="index"
        :optionIndex="optIndex"
        :optionName="option.optionName"
        :optionCheck="option.optionCheck"
      />
    </div>
  </div>
</template>
<script>
import Setting from "./Setting.vue";

export default {
  name: "Settings",
  components: {
    Setting,
  },
  data() {
    return {
      // settingIndex: 0,
    };
  },
  props: {
    settings: Array,
  },
  emits: ["change-settings"],
  methods: {
    handleChange(OptionToChange) {
      // console.log(OptionToChange);
      this.$emit("change-settings", OptionToChange);
    },
  },
};
</script>

<style scoped>
h3 {
  text-transform: uppercase;
}
@media (min-width: 300px) {
  h3 {
    font-size: 0.7rem;
    margin: 10px 0px;
  }
}
@media (min-width: 425px) {
  h3 {
    font-size: 1rem;
    margin: 15px 0px;
  }
}

/* TABLET */
@media (min-width: 768px) {
  h3 {
    font-size: 1.25rem;
    margin: 20px 0px;
  }
}

/* DESKTOP */
@media (min-width: 1024px) {
}
</style>
