<template>
  <div
    class="setting"
    :style="
      optionCheck
        ? { 'text-decoration': 'none' }
        : { 'text-decoration': 'line-through', color: 'gray' }
    "
    @click="$emit('change-setting', { settingIndex, optionIndex })"
  >
    <label>{{ optionName }}</label>
  </div>
</template>

<script>
export default {
  name: "Setting",
  props: {
    settingIndex: Number,
    optionName: String,
    optionIndex: Number,
    optionCheck: Boolean,
    settings: Array,
  },
  emits: ["change-setting"],
};
</script>

<style scoped>
.setting {
  text-align: center;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.8s ease;
}

@media (min-width: 300px) {
  label {
    font-size: 0.6rem;
  }
}
@media (min-width: 425px) {
  label {
    font-size: 0.8rem;
  }
}

/* TABLET */
@media (min-width: 768px) {
  .setting {
    margin: 20px 0px;
  }
  label {
    font-size: 1rem;
  }
}

/* DESKTOP */
@media (min-width: 1024px) {
  label {
    /* font-size: 1.rem; */
  }
}
</style>
