<template>
  <form @submit="onSubmit">
    <BasicSetting
      @basic-setting="basicSetting"
      @toggle-advance-setting="toggleAdvanceSetting"
      :showAdvanceSetting="showAdvanceSetting"
      :colour="colour"
    />

    <transition name="transition">
      <div v-show="showAdvanceSetting" class="advance-setting">
        <Settings @change-settings="handleChange" :settings="settings" />
      </div>
    </transition>

    <Button color="black" text="submit" @click="onSubmit($event)" />
  </form>
</template>

<script>
import Button from "./Button.vue";
import BasicSetting from "./BasicSetting.vue";
import Settings from "./Settings.vue";

export default {
  name: "AdvanceSetting",
  data() {
    return {
      totalCharacter: 3,
      showAdvanceSetting: false,
    };
  },
  components: {
    Button,
    BasicSetting,
    Settings,
  },
  props: {
    settings: Array,
    colour: String,
  },
  emits: ["change-advSetting", "generate"],
  methods: {
    basicSetting(totalCharacter) {
      this.totalCharacter = totalCharacter;
    },
    toggleAdvanceSetting() {
      this.showAdvanceSetting = !this.showAdvanceSetting;
    },
    onSubmit(e) {
      e.preventDefault();
      this.$emit("generate", this.totalCharacter);
    },
    handleChange(OptionToChange) {
      this.$emit("change-advSetting", OptionToChange);
    },
  },
};
</script>

<style scoped>
.advance-setting {
  display: grid;
  grid-template-columns: repeat(3, minmax(80px, 1fr));
  justify-content: space-between;

  text-align: center;
  margin: 30px 0px;
  transition: all 1s ease;

  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 20px 20px;
  background-color: white;
  opacity: 0.8;
}
.transition-enter-active,
.transition-leave-active {
  transition: opacity 0.5s ease;
}

.transition-enter-from,
.transition-leave-to {
  opacity: 0;
}

@media (min-width: 300px) {
  .advance-setting {
    padding: 10px 0px;
  }
}
/* TABLET */
@media (min-width: 768px) {
  .advance-setting {
    /* padding: 20px 0px; */
  }
}

/* DESKTOP */
@media (min-width: 1024px) {
}
</style>
