<template>
  <transition name="fade">
    <div v-if="showCharacters">
      <Characters
        :settings="settings"
        :characters="characters"
        :colour="colour"
      />
      <Table :characters="characters" :colour="colour" />
    </div>
  </transition>
</template>
<script>
import Characters from "../components/Characters.vue";
import Table from "../components/Table.vue";

export default {
  name: "Home",
  components: {
    Characters,
    Table,
  },
  props: {
    colour: Object,
    settings: Array,
    characters: Array,
    showCharacters: Boolean,
  },
};
</script>
<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-active {
  opacity: 0;
}
</style>
