<template>
  <button @click="onClick" :style="{ background: color }" class="btn">
    {{ text }}
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    text: String,
    color: String,
  },
  methods: {
    onClick(e) {
      e.preventDefault();
      this.$emit("btn-click");
    },
  },
};
</script>

<style scoped>
.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;

  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.8rem;
  font-family: inherit;
  transition: all 0.7s ease;
}
.btn:focus {
  outline: none;
}
.btn:hover {
  -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.6);
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.6);
}
.btn:active {
  transform: scale(0.98);
}
.btn-block {
  display: block;
  width: 100%;
}

@media (min-width: 300px) {
  .btn {
    font-size: 0.5rem;
    padding: 5px 10px;
  }
}
@media (min-width: 425px) {
  .btn {
    font-size: 0.65rem;
    padding: 8px 15px;
  }
}

/* TABLET */
@media (min-width: 768px) {
  .btn {
    font-size: 0.8rem;
    padding: 10px 20px;
  }
}

/* DESKTOP */
@media (min-width: 1024px) {
}
</style>
